.fg {
  position: relative;
  margin: 0;
  input {
    background: #F2F1F6;
    border: 1px solid #DBD7E9;
    border-radius: 10px;
    padding: 0 10px;
    height: 50px;
    width: 100%;
    font-size: 16px;
    line-height: 150%;
    @include fontWeight(350,$font2);
    &::placeholder {
      color: $gray;
    }
    &:focus{
      background-color: transparent;
      border-color: $primary;
    }
  }
  &__icon{
    position: absolute;
    left: 20px;
    bottom: 50%;
    transform: translateY(50%);
    fill:$gray;
    &~input{
      padding-left: 44px;
    }
  }
}

.fg-pass{
  &__btn{
    position: absolute;
    bottom: 50%;
    transform: translateY(50%);
    right: 20px;
    width: 12px;
    height: 12px;
    background-image: url(../img/eye.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% auto;
    &._show-pass{
      background-image: url(../img/eye2.svg);
    }
  }
  input{
    padding-right: 44px;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        border-color: $primary;
        background-image: url(../img/check.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 85% auto;
      }
    }
  }
  label {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 24px;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: 0.01em;
    @include fontWeight(350,$font2);
    cursor: pointer;
    &:before {
      content: '';
      z-index: 1;
      border: 1px solid #DBD7E9;
      border-radius: 10px;
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      margin-right: 10px;
      display: block;
    }
  }
}
