.cart-card{
  padding: 10px 0;
  display: grid;
  grid-template-columns: 50px auto 50px;
  align-items: center;
  gap: 10px;
  border-bottom: 1px dashed #DBD7E9;
  &__icon{
    border-radius: 10px;
    background: #F2F1F6;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 90%;
      max-height: 90%;
    }
  }
  &__title{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__price{
    font-weight: 500;
    margin-top: 5px;
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__remove{
    svg{
      fill:#CECBDA;
    }
    &:hover{
      svg{
        fill: $black;
      }
    }
  }
  &__count{
    margin-right: 10px;
    font-weight: 600;
  }
}

.product-card{
  background: #FFFFFF;
  box-shadow: 0px 5px 20px #F2F1F6;
  border-radius: 20px;
  padding: 0 25px 25px;
  position: relative;
  display: flex;
  flex-direction: column;
  transition: shadow .3s, transform 1.6s,opacity 1.6s;
  &:hover{
    box-shadow: 0px 5px 20px #D5D0E7;
  }
  &__top{
    display: block;
    position: relative;
    z-index: 2;
    padding-bottom: 62.51%;
    margin-top: -20px;
    color: #fff;
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    padding: 30px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
  }
  &__icon{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 23px;
    flex-shrink: 0;
    min-width: 50px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    font-size: 26px;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
  &__text{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 10px 20px;
    font-size: 13px;
    line-height: 130%;
    letter-spacing: 0.01em;
    color: #B6C0EF;
  }
  &__badge{
    text-transform: uppercase;
    position: absolute;
    left: 37px;
    top: 0;
    transform: translateY(-50%);
    height: 25px;
    padding: 0 15px;
    display: flex;
    align-items: center;
    border-radius: 50px;
    font-weight: 600;
    font-size: 10px;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
  &__title{
    display: block;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 0.01em;
    margin: 20px auto 0;
    max-width: 230px;
  }
  &__prices{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0 10px;
  }
  &__price1{
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.01em;
    text-decoration-line: line-through;
    margin-right: 12px;
    color: #7B739E;
  }
  &__price2{
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    letter-spacing: 0.01em;
  }
  &__btn{
    width: 139px;
    margin: auto auto 0;
    svg{
      margin-right: 10px;
    }
  }
}

.gift-card{
  display: block;
  padding: 0 15px 10px;
  background: #FFFFFF;
  border-radius: 10px;
  &__top{
    margin-top: -20px;
    padding-bottom: 55.6%;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    background: #F2F1F6;
    &:hover{
      color: #fff;
      .gift-card__bg{
        opacity: 1;
      }
      svg{
        fill: #fff;
      }
    }
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(80.25% 171.06% at 32.76% 27.25%, #0B3A71 0%, #2568B7 100%);
    background-size: 110% 110%;
    transition: all .3s;
    opacity: 0;
    border-radius: 10px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.01em;
    svg{
      width: 40px;
      height: auto;
      margin-right: 10px;
    }
  }
  &__price{
    text-align: center;
    font-weight: 600;
    margin-top: 10px;
  }
}

.feedback-card{
  &:not(:last-child){
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #F2F1F6;
  }
  &__name{
    font-weight: 500;
    color: $gray;
  }
  &__date{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    color: $gray;
    margin-top: 5px;
    svg{
      margin-right: 10px;
      fill:$gray;
    }
  }
  &__text{
    @include fontWeight(350,$font2);
    margin-top: 8px;
  }
  &__answer{
    padding: 15px 30px;
    background: #F6F6FA;
    border-radius: 20px;
    width: calc(100% - 70px);
    margin: 10px 0 0 auto;
  }
  &__admin{
    font-weight: 500;
    display: flex;
    align-items: center;
    svg{
      margin-right: 10px;
    }
  }
}

.faq-card{
  padding: 20px 30px;
  border: 1px solid #F2F1F6;
  border-radius: 20px;
  cursor: pointer;
  &._active{
    .faq-card__show{
      svg{
        transform: rotate(180deg);
      }
    }
  }
  &:not(:last-child){
    margin-bottom: 10px;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 400;
    svg{
      margin-left: 10px;
      transition: all .4s;
    }
  }
  &__hidden{
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px dashed #DBD7E9;
    display: none;
  }
}


@media screen and (max-width: 768px){
  .product-card{
    &__content{
      padding-left: 20px;
      padding-right: 20px;
    }
    &__icon{
      max-width: 60px;
      height: 70px;
      margin-right: 17px;
    }
    &__name{
      font-size: 12.0376px;
    }
    &__desc{
      font-size: 19.5611px;
    }
    &__badge{
      left: 16px;
    }
    &__title,
    &__prices{
      margin-top: 10px;
    }
    &__text{
      padding: 7px 15px;
      font-size: 9.78056px;
    }
  }

  .feedback-card{
    &__answer{
      padding-left: 20px;
      padding-right: 20px;
      width: 100%;
    }
  }

  .faq-card{
    padding-left: 20px;
    padding-right: 20px;
  }
}
